.blur {
    filter: blur(3px);
    -webkit-filter: blur(3px);
    -moz-filter: blur(3px);
    -o-filter: blur(3px);
    -ms-filter: blur(3px);

    animation: blurKeyframe 10s ease-out 0s 1;
    -webkit-animation: blurKeyframe 10s ease-out 0s 1;
    -moz-animation: blurKeyframe 10s ease-out 0s 1;
}

@keyframes blurKeyframe {

    from {
        -webkit-filter: blur(50px);
        -moz-filter: blur(50px);
        -o-filter: blur(50px);
        -ms-filter: blur(50px);
    }

    to {
        -webkit-filter: blur(3px);
        -moz-filter: blur(3px);
        -o-filter: blur(3px);
        -ms-filter: blur(px);
    }

}

.disabledPointers {
    pointer-events: none;
}

.oswald {
    font-family: 'Oswald', sans-serif;
    font-size: 50px;
}